import Link from "next/link";

import { StatsCard } from "@/components/StatsCard";
import { OPENROUTER_OPENCLAW_APP_PAGE, getOpenClawEcosystemSnapshot } from "@/lib/openrouter-ecosystem";

const formatNumber = (value: number): string => Intl.NumberFormat("en-US").format(value);
const formatCompact = (value: number): string =>
  Intl.NumberFormat("en-US", { notation: "compact", maximumFractionDigits: 1 }).format(value);

const formatDate = (date: string): string =>
  new Date(`${date}T00:00:00Z`).toLocaleDateString("en-US", {
    month: "short",
    day: "numeric",
  });

const formatDateTime = (date: string): string =>
  new Date(date).toLocaleString("en-US", {
    month: "short",
    day: "numeric",
    hour: "numeric",
    minute: "2-digit",
  });

export default async function EcosystemPage() {
  const snapshot = await getOpenClawEcosystemSnapshot();

  return (
    <main className="oc-shell">
      <header className="flex flex-col gap-4 md:flex-row md:items-end md:justify-between">
        <div>
          <p className="oc-kicker">OpenClaw Ecosystem</p>
          <h1 className="mt-1 text-4xl font-semibold md:text-5xl">Ecosystem Pulse</h1>
          <p className="mt-2 max-w-3xl text-sm text-[color:var(--text-secondary)]">
            Community token flow for <span className="font-semibold">{snapshot?.appTitle ?? "OpenClaw"}</span> through
            OpenRouter. This page is read-only and tracks public aggregate analytics only.
          </p>
        </div>
        <div className="flex flex-wrap items-center gap-2">
          <Link href="/" className="oc-nav-link">
            Home
          </Link>
          <Link href="/dashboard" className="oc-nav-link">
            Dashboard
          </Link>
          <Link href="/leaderboard" className="oc-nav-link">
            Leaderboard
          </Link>
        </div>
      </header>

      {!snapshot ? (
        <section className="oc-panel mt-7 p-6">
          <p className="oc-kicker">Data unavailable</p>
          <h2 className="mt-2 text-2xl font-semibold">OpenRouter ecosystem feed is temporarily unavailable.</h2>
          <p className="mt-2 text-sm text-[color:var(--text-secondary)]">
            The feed can fail intermittently. Retry shortly, or view the source directly.
          </p>
          <a href={OPENROUTER_OPENCLAW_APP_PAGE} target="_blank" rel="noreferrer" className="oc-button-primary mt-5 px-4 py-2 text-sm">
            Open source feed
          </a>
        </section>
      ) : (
        <>
          <section className="oc-panel mt-7 p-4 md:p-5">
            <div className="flex flex-wrap items-center justify-between gap-3">
              <p className="text-sm text-[color:var(--text-secondary)]">
                Source:{" "}
                <a href={OPENROUTER_OPENCLAW_APP_PAGE} target="_blank" rel="noreferrer" className="underline decoration-dotted">
                  OpenRouter app analytics
                </a>
              </p>
              <p className="text-xs uppercase tracking-[0.14em] text-[color:var(--text-muted)]">
                Refreshed {formatDateTime(snapshot.fetchedAt)}
              </p>
            </div>
            <div className="oc-trust mt-4 rounded-lg px-3 py-2 text-xs text-[color:var(--text-secondary)]">
              Safety: this tab only reads public OpenRouter aggregate metrics. It does not connect to your gateway and
              cannot execute commands.
            </div>
          </section>

          <section className="mt-7 grid gap-4 sm:grid-cols-2 xl:grid-cols-5">
            <StatsCard label="24h Tokens" value={formatNumber(snapshot.last24hTokens)} caption="Latest day aggregate" tone="cyan" />
            <StatsCard label="7d Tokens" value={formatNumber(snapshot.sevenDayTokens)} caption="Rolling week" tone="coral" />
            <StatsCard label="30d Tokens" value={formatNumber(snapshot.thirtyDayTokens)} caption="Rolling month" tone="cyan" />
            <StatsCard label="Tracked Days" value={formatNumber(snapshot.trackedDays)} caption="In current source window" tone="coral" />
            <StatsCard label="Active Models" value={formatNumber(snapshot.activeModelsToday)} caption="Models used today" tone="cyan" />
          </section>

          <section className="mt-6 grid gap-4 xl:grid-cols-2">
            <article className="oc-panel p-4 md:p-5">
              <h2 className="text-xl font-semibold">Daily token trend</h2>
              <p className="mt-1 text-sm text-[color:var(--text-secondary)]">Recent OpenClaw ecosystem throughput (last 14 days).</p>
              <ul className="mt-4 space-y-3">
                {snapshot.daily.slice(-14).map((point) => {
                  const max = Math.max(...snapshot.daily.slice(-14).map((entry) => entry.totalTokens), 1);
                  const width = Math.max(4, Math.round((point.totalTokens / max) * 100));
                  return (
                    <li key={point.date} className="grid gap-2 text-sm md:grid-cols-[72px_1fr_auto] md:items-center">
                      <span className="text-[color:var(--text-muted)]">{formatDate(point.date)}</span>
                      <div className="h-2 overflow-hidden rounded-full bg-white/7">
                        <div
                          className="h-full rounded-full bg-gradient-to-r from-[color:var(--cyan-mid)] to-[color:var(--cyan-bright)]"
                          style={{ width: `${width}%` }}
                        />
                      </div>
                      <span className="font-[family-name:var(--font-mono)] text-[color:var(--text-secondary)]">
                        {formatCompact(point.totalTokens)}
                      </span>
                    </li>
                  );
                })}
              </ul>
            </article>

            <article className="oc-panel p-4 md:p-5">
              <h2 className="text-xl font-semibold">Current day mix</h2>
              <p className="mt-1 text-sm text-[color:var(--text-secondary)]">
                Top contributors on {snapshot.latestDate ? formatDate(snapshot.latestDate) : "latest available day"}.
              </p>
              <ul className="mt-4 space-y-3">
                {(snapshot.daily[snapshot.daily.length - 1]
                  ? Object.entries(snapshot.daily[snapshot.daily.length - 1].models)
                      .filter(([model, tokens]) => model !== "Others" && tokens > 0)
                      .sort((left, right) => right[1] - left[1])
                      .slice(0, 8)
                  : []
                ).map(([model, tokens]) => {
                  const share =
                    snapshot.last24hTokens > 0 ? Math.max(4, Math.round((tokens / snapshot.last24hTokens) * 100)) : 0;
                  return (
                    <li key={model}>
                      <div className="flex items-center justify-between gap-3 text-sm">
                        <span className="truncate">{model}</span>
                        <span className="font-[family-name:var(--font-mono)] text-[color:var(--text-secondary)]">
                          {formatCompact(tokens)}
                        </span>
                      </div>
                      <div className="mt-1.5 h-1.5 overflow-hidden rounded-full bg-white/7">
                        <div
                          className="h-full rounded-full bg-gradient-to-r from-[color:var(--coral-mid)] to-[color:var(--coral-bright)]"
                          style={{ width: `${share}%` }}
                        />
                      </div>
                    </li>
                  );
                })}
              </ul>
              <p className="mt-4 text-xs text-[color:var(--text-muted)]">
                Dominant model:{" "}
                <span className="font-semibold text-[color:var(--text-secondary)]">
                  {snapshot.dominantModel ?? "Unknown"}
                </span>{" "}
                ({formatCompact(snapshot.dominantModelTokens)} tokens).
              </p>
            </article>
          </section>

          <section className="mt-6">
            <article className="oc-panel p-4 md:p-5">
              <h2 className="text-xl font-semibold">Monthly model leaderboard (OpenRouter)</h2>
              <p className="mt-1 text-sm text-[color:var(--text-secondary)]">
                Highest-volume models used by the OpenClaw app audience.
              </p>
              <div className="mt-4 space-y-3">
                {snapshot.topModels.slice(0, 12).map((model) => (
                  <div key={model.model}>
                    <div className="flex items-center justify-between gap-3 text-sm">
                      <span className="truncate">{model.model}</span>
                      <span className="font-[family-name:var(--font-mono)] text-[color:var(--text-secondary)]">
                        {formatCompact(model.totalTokens)} ({(model.share * 100).toFixed(1)}%)
                      </span>
                    </div>
                    <div className="mt-1.5 h-1.5 overflow-hidden rounded-full bg-white/7">
                      <div
                        className="h-full rounded-full bg-gradient-to-r from-[color:var(--cyan-mid)] to-[color:var(--cyan-bright)]"
                        style={{ width: `${Math.max(4, Math.round(model.share * 100))}%` }}
                      />
                    </div>
                  </div>
                ))}
              </div>
            </article>
          </section>
        </>
      )}
    </main>
  );
}
