interface StatsCardProps {
  label: string;
  value: string;
  caption?: string;
  tone?: "cyan" | "coral";
}

const toneBorder = (tone: "cyan" | "coral") =>
  tone === "coral"
    ? "from-[color:var(--coral-bright)]/80 to-transparent"
    : "from-[color:var(--cyan-bright)]/80 to-transparent";

const toneGlow = (tone: "cyan" | "coral") =>
  tone === "coral" ? "shadow-[0_0_30px_rgba(255,92,87,0.09)]" : "shadow-[0_0_30px_rgba(25,226,197,0.09)]";

export function StatsCard({ label, value, caption, tone = "cyan" }: StatsCardProps) {
  return (
    <article className={`oc-panel p-4 ${toneGlow(tone)}`}>
      <div className={`pointer-events-none absolute inset-x-0 top-0 h-px bg-gradient-to-r ${toneBorder(tone)}`} />
      <p className="text-xs uppercase tracking-[0.2em] text-[color:var(--text-muted)]">{label}</p>
      <p className="mt-2 text-3xl font-semibold leading-none">{value}</p>
      {caption ? <p className="mt-1 text-sm text-[color:var(--text-secondary)]">{caption}</p> : null}
    </article>
  );
}
